* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

:global html,
:global body,
:global #root {
  width: 100%;
  height: 100%;
  min-width: 1440px;
  min-height: 990px;
  background: #283644 url("./assets/imgs/bg.png") repeat top center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Helvetica, 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', SimSun, sans-serif; }

:global li {
  list-style: none; }
